import React, {Component} from 'react';
import './styles/WhatIsBoccia.css';

class WhatIsBoccia extends Component {
	constructor(){
		super();
		this.state = {  
			active:1
	  	}

	}

	handleclick=(evt)=>{
		this.setState({active:evt.target.value});
			console.log(this.state.active)
	}
	handlearrow=(evt)=>{
		if(evt.target.value==1){
			if(this.state.active==1){
				this.setState({active:6})
			}
			else{
				this.setState({active:parseInt(this.state.active)-1})
			}
		}
		else if(evt.target.value==2){
			if(this.state.active==6){
				this.setState({active:1})
			}
			else{
				this.setState({active:parseInt(this.state.active)+1})
			}
		}		
	}

	render(){

	let GetDivButton=()=>{
		if(this.state.active==2)
			{
				return(
					<div id="WhatIsMainButtonDiv">
						<button className="WhatIsMainButton WhatIsMainButtonActive rightcurved"><p>   爪'</p></button>
						<button className="WhatIsMainButton"><p>  注</p></button>
						<button className="WhatIsMainButton"><p> 砖拽</p></button>
						<button className="WhatIsMainButton"><p>专转 砖专</p></button>
						<button className="WhatIsMainButton leftcurved"><p>转专</p></button>	
					</div>
				);
			}
			if(this.state.active==3)
			{
				return(
					<div id="WhatIsMainButtonDiv">
						<button className="WhatIsMainButton rightcurved"><p>   爪' </p></button>
						<button className="WhatIsMainButton WhatIsMainButtonActive"><p>  注</p></button>
						<button className="WhatIsMainButton"><p> 砖拽</p></button>
						<button className="WhatIsMainButton"><p>专转 砖专</p></button>
						<button className="WhatIsMainButton leftcurved"><p>转专 </p></button>		
					</div>
				);
			}

			if(this.state.active==4)
				{
					return(
						<div id="WhatIsMainButtonDiv">
							<button className="WhatIsMainButton rightcurved"><p>   爪'</p></button>
							<button className="WhatIsMainButton"><p>  注</p></button>
							<button className="WhatIsMainButton WhatIsMainButtonActive"><p> 砖拽</p></button>
							<button className="WhatIsMainButton"><p>专转 砖专</p></button>
							<button className="WhatIsMainButton leftcurved"><p>转专 </p></button>	
						</div>
					);
				}
			if(this.state.active==5)
				{
					return(
						<div id="WhatIsMainButtonDiv">
							<button className="WhatIsMainButton rightcurved"><p>   爪'</p></button>
							<button className="WhatIsMainButton"><p>  注</p></button>
							<button className="WhatIsMainButton"><p> 砖拽</p></button>
							<button className="WhatIsMainButton WhatIsMainButtonActive "><p>专转 砖专</p></button>
							<button className="WhatIsMainButton leftcurved"><p>转专 </p></button>	
						</div>
					);
				}	
			if(this.state.active==6)
				{
					return(
						<div id="WhatIsMainButtonDiv">
							<button className="WhatIsMainButton rightcurved"><p>   爪'</p></button>
							<button className="WhatIsMainButton"><p>  注</p></button>
							<button className="WhatIsMainButton"><p> 砖拽</p></button>
							<button className="WhatIsMainButton"><p>专转 砖专</p></button>
							<button className="WhatIsMainButton WhatIsMainButtonActive leftcurved"><p>转专 </p></button>	
						</div>
					);
				}
	}

	let GetDivContent=()=>{
			
			if(this.state.active==1)
			{
				return(
					<div id="WhatIsContentDivFirstState">
						<p>住专 砖 拽爪专 砖  砖转 砖专转</p>
						<p>住专 砖 拽爪专 砖  砖转 砖专转</p>
						<div id="WhatIsContentDivFirstStatePage">
								<button className="WhatIsFirstStatePageButton" onClick={this.handleclick} value="2">01<br/>  爪'</button>
								<button className="WhatIsFirstStatePageButton" onClick={this.handleclick} value="3">02<br/>  注</button>
								<button className="WhatIsFirstStatePageButton" onClick={this.handleclick} value="4">03<br/> 砖拽</button>
								<button className="WhatIsFirstStatePageButton" onClick={this.handleclick} value="5">04<br/>专转 砖专</button>
								<button className="WhatIsFirstStatePageButton" onClick={this.handleclick} value="6">05<br/>转专</button>		
						</div>
					</div>
				)
			}

			if(this.state.active==2)
			{
				return(
					<div id="WhatIsContentDiv">
								<p id="WhatIsContentText">1</p>						
					</div>
				)
			}
			if(this.state.active==3)
			{
				return(
					<div id="WhatIsContentDiv">
								<p id="WhatIsContentText">
								2
								</p>						
					</div>
				)
			}

			if(this.state.active==4)
				{
					return(
						<div id="WhatIsContentDiv">
									<p id="WhatIsContentText">3</p>						
						</div>
					)
				}
			if(this.state.active==5)
				{
					return(
						<div id="WhatIsContentDiv">
									<p id="WhatIsContentText">4</p>						
						</div>
					)				
				}	
			if(this.state.active==6)
				{
					return(
						<div id="WhatIsContentDiv">
									<p id="WhatIsContentText">5</p>						
						</div>
					)				
				}		
	}

	let GetDivFrontBack=()=>{
		if(this.state.active!=1){
			return(
				<div>
					<div id="BackButton">
						<button value="1" onClick={this.handlearrow} className="FrontBackButtons">
							『  拽
						</button>
					</div>
					<div id="FrontButton">
						<button value="2" onClick={this.handlearrow} className="FrontBackButtons">
							 
						</button>
					</div>		
			</div>
			);
		}
	}


		return(
			<div>
				
				<div className="sidenavbar" >
					<p className="sidenavtabs"> <a href="./#/WhatIsBoccia">LOGO</a></p>
		        	<p className="activesidenavtabs sidenavtabs"> <a href="./#/WhatIsBoccia">专 爪'</a></p>
		        	<p className="sidenavtabs"> <a href="./#/BocciaClasses">住</a></p>
		         	<p className="sidenavtabs"> <a href="./#/">专砖</a></p>
		         	<p className="sidenavtabs"> <a href="./#/">爪</a></p>
		        	<p className="sidenavtabs"> <a href="./#/">拽 砖驻</a></p>
		         	<p className="sidenavtabs"> <a href="./#/">转驻拽</a></p>
		          	<p className="sidenavtabs"> <a href="./#/">住专</a></p> 
		 		 	<p className="sidenavtabs"> <a href="./#/">转专  </a></p>
		 		 	<p className="sidenavtabs"> <a href="./#/"> 爪专 拽砖专 </a></p> 
				</div>
				<div id="WhatIsMain">	
					{GetDivButton()}
					{GetDivContent()}
					{GetDivFrontBack()}					
				</div>				
			</div>

		)

	}
}

export default WhatIsBoccia;